<template>
  <v-container fluid grid-list-md>
    <v-layout row wrap>
      <v-flex xs12 sm12 md9>
        <v-data-iterator
          :items="items"
          :items-per-page.sync="itemsPerPage"
          :page="page"
          hide-default-footer
        >
          <template v-slot:header>
            <v-toolbar class="mb-2" dense color="#042539" dark flat>
              <v-toolbar-title>
                <v-btn color="yellow darken-2" v-on:click="$emit('search-result-back',1)">
                  <v-icon>fa-arrow-left</v-icon>
                </v-btn>
              </v-toolbar-title>
            </v-toolbar>
          </template>
          <template v-slot:default="props">
            <v-layout wrap>
              <v-flex
                class="ma-0 pa-1"
                v-for="item in props.items"
                :key="item.name"
                xs12
                sm6
                md2
                lg3
              >
                <v-card height="150" elevation="24" max-width="444" class="mx-auto">
                  <v-carousel
                    :continuous="false"
                    :cycle="cycle"
                    :show-arrows="false"
                    hide-delimiter-background
                    delimiter-icon="mdi-minus"
                    height="100"
                  >
                    <v-carousel-item v-for="(slide, i) in slides" :key="i">
                      <v-sheet :color="colors[i]" height="100" tile>
                        <v-layout align-center fill-height justify-center>
                          <div class="display-3">{{ slide }} Slide</div>
                        </v-layout>
                      </v-sheet>
                    </v-carousel-item>
                  </v-carousel>
                  <v-btn v-on:click="$emit('search-result-select',1)" color="success">View</v-btn>
                </v-card>
              </v-flex>
            </v-layout>
          </template>
          <template v-slot:footer>
            <v-layout justify-center align-center>
              <v-toolbar class="mt-2" color="#042539" dense dark flat>
                <v-pagination color="yellow" v-model="page" :length="10" :total-visible="7"></v-pagination>
              </v-toolbar>
            </v-layout>
          </template>
        </v-data-iterator>
      </v-flex>
      <v-flex xs12 sm12 md3>
        <v-window v-model="window" full-height vertical>
          <v-window-item v-for="n in length" :key="n">
            <v-card flat>
              <v-sheet color="blue" height="400">AD Space</v-sheet>
            </v-card>
          </v-window-item>
        </v-window>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  name: "SearchResult",

  data() {
    return {
      length: 3,
      window: 0,
      autorun: true,
      itemsPerPageArray: [8],
      search: "",
      filter: {},
      sortDesc: false,
      page: 1,
      itemsPerPage: 8,
      sortBy: "name",
      keys: [
        "Name",
        "Calories",
        "Fat",
        "Carbs",
        "Protein",
        "Sodium",
        "Calcium",
        "Iron"
      ],
      items: [
        {
          name: "Frozen Yogurt"
        },
        {
          name: "Ice cream sandwich"
        },
        {
          name: "Eclair"
        },
        {
          name: "Cupcake"
        },
        {
          name: "Gingerbread"
        },
        {
          name: "Jelly bean"
        },
        {
          name: "Lollipop"
        },
        {
          name: "Honeycomb"
        },
        {
          name: "Donut"
        },
        {
          name: "KitKat"
        },
        {
          name: "KitKat"
        },
        {
          name: "KitKat"
        },
        {
          name: "KitKat"
        },
        {
          name: "KitKat"
        },
        {
          name: "KitKat"
        },
        {
          name: "KitKat"
        },
        {
          name: "KitKat"
        }
      ],
      colors: ["green", "red", "secondary"],
      cycle: false,
      slides: ["First", "Second", "Third"]
    };
  },
  computed: {
    numberOfPages() {
      return Math.ceil(this.items.length / this.itemsPerPage);
    },
    filteredKeys() {
      return this.keys.filter(key => key !== `Name`);
    }
  },
  methods: {
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    }
  },

  created() {
    setInterval(() => {
      if (!this.autorun) return;
      if (++this.window >= this.length) this.window = 0;
    }, 1000);
  }
};
</script>

